import {
  Box,
  Flex,
  Text,
  IconButton,
  Button,
  Stack,
  useDisclosure,
  useBreakpointValue,
  Image,
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons";

const MotionBox = motion(Box);

export function Header() {
  const { isOpen, onToggle } = useDisclosure();
  const isMobile = useBreakpointValue({ base: true, md: false });

  return (
    <Box as="header" bg="black" color="white" px={6} py={4} boxShadow="lg" position="fixed" top="0" left="0" zIndex="999999" w="full" >
      <Flex align="center" justify="space-between">
        {/* Logo */}
        <Flex align="center">
          <Image src="/taxator.svg" h="full" borderRadius={"full"}/>
          <Text
            ml={2}
            fontSize="2xl"
            fontWeight="bold"
            letterSpacing="wide"
            color="yellow.300"
          >
            TAX
          </Text>
        </Flex>

        {/* Menu - Desktop */}
        {!isMobile && (
          <Stack direction="row" spacing={6}>
            {["About", "Tokenomics", "Roadmap", "Community"].map((item) => (
              <MotionBox
                key={item}
                whileHover={{ scale: 1.1 }}
                transition={{ duration: 0.2 }}
              >
                <Button variant="ghost" color="yellow.200" fontSize="lg">
                  {item}
                </Button>
              </MotionBox>
            ))}
          </Stack>
        )}

        {/* Menu Mobile - Boto */}
        {isMobile && (
          <IconButton
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            onClick={onToggle}
            aria-label="Open Menu"
            variant="ghost"
            color="yellow.300"
            fontSize="2xl"
          />
        )}
      </Flex>

      {/* Menu Mobile - Itens */}
      {isMobile && isOpen && (
        <MotionBox
          initial={{ opacity: 0, y: -10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.3 }}
          mt={4}
        >
          <Stack spacing={4} textAlign="center">
            {["About", "Tokenomics", "Roadmap", "Community"].map((item) => (
              <Button key={item} variant="ghost" fontSize="lg">
                {item}
              </Button>
            ))}
          </Stack>
        </MotionBox>
      )}
    </Box>
  );
}
